@import url('https://fonts.googleapis.com/css2?family=PT+Sans+Caption:wght@700&family=Poppins:wght@200;400&family=Raleway:wght@100;600&family=Source+Sans+Pro:wght@200&family=Work+Sans:wght@400;500&display=swap');


.breakpoint__large-up {
  .contact {
    display: flex;
  }

  .contact-left {
    flex: 0.5;
    padding: 50px;
  }
  
  .contact-right {
    flex: 0.6;
  }

  .contact-form {
    display: flex;
    flex-direction: column;
    padding: 10px 0;
  }

  .contact-title {
    padding: 10px 0;
  }

  .contact-info {
    padding: 20px 0;
  }
   
}

.breakpoint__medium-down {
  .contact {
    display: flex;
    flex-direction: column;
    padding: 50px;
  }

  .contact-info {
    padding: 20px 0;
  }

  .contact-title {
    color: rgb(241, 177, 14);
    padding: 20px 0;
  }

  .contact-form {
    display: flex;
    flex-direction: column;
  }

  .contact-row {
    display: flex;
  }

  #name, #email {
    flex: 0.5;
  }

  #name {
    margin-right: 20px;
  }

  .contact-form {
    padding-top: 20px;
    padding-bottom: 40px;
  }
  
}

.contact-title {
  font-size: 40px;
  font-weight: bold;
  color: rgb(241, 177, 14);
}

.contact-info {
  color: white;
  font-size: 18px;
}

#name, #phone, #email, #message {
  background-color: #1e1e1e;
  margin-bottom: 20px;
  padding: 15px;
  border: 1px solid rgb(241, 177, 14);
  font-family: 'Raleway';
  color: white;
  font-size: 18px;
}

#name, #phone, #email, #message, #submit-button:focus {
  outline: none;
}

#submit-button {
  padding: 10px;
  border: 1px solid rgb(241, 177, 14);
  background-color: #1E1E1E;
  font-family: 'Raleway';
  color: darkgray;
  font-size: 18px;
}

#submit-button:hover {
  background-color: rgb(241, 177, 14);
  transition: 0.5s;
  opacity: 0.8;
  color: #1e1e1e;
  cursor: pointer;
}

.contact-map-box {
  display: flex;
  flex-direction: column;
  font-size: 18px;
}

.contact-soc {
  text-decoration: none;
  padding-left: 8px;
}

.contact-box-info {
  display: flex;
  align-items: center;
}