.breakpoint__large-up {
  .project-image {
    width: 650px;
    height: 350px;
    object-fit: fill;
    border: 1px solid black;
  }

  .project-hidden {
    font-size: 20px;
    color: white;
    position: absolute;
    top: 20px;
    left: 20px;
    width: 652px;
    height: 352px;
    background-color: rgba(0, 0, 0, 0.6);
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    transition: opacity 0.25s;
  }
}

.breakpoint__medium-down {
  .project-image {
    height: 225px;
    width: 400px;
    object-fit: fill;
    border: 1px solid black;
  }

  .project-hidden {
    font-size: 20px;
    color: white;
    position: absolute;
    top: 20px;
    left: 20px;
    width: 402px;
    height: 227px;
    background-color: rgba(0, 0, 0, 0.6);
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    transition: opacity 0.25s;
  }
 
}


.project-container {
  padding: 20px;
  position: relative;
}

.project-hidden:hover {
  opacity: 1;
  transition: opacity 0.25s;
  cursor: pointer;
}

.project-link {
  padding: 20px;
  text-decoration: none;
  color: rgb(241, 177, 14);
}

.project-link:hover {
  color: white;
}

.project-hidden > * {
  transform: translateY(20px);
  transition: transform 0.25s;
}

.project-hidden:hover > * {
  transform: translateY(0);
  transition: transform 0.25s;
}