@import url('https://fonts.googleapis.com/css2?family=PT+Sans+Caption:wght@700&family=Poppins:wght@200;400&family=Raleway:wght@100;600&family=Source+Sans+Pro:wght@200&family=Work+Sans:wght@400;500&display=swap');

.breakpoint__medium-up { 
  .home-intro {
    font-size: 65px;
    padding: 10px 0;
    font-weight: bold;
  }

  .home-button {
    font-family: 'Raleway';
    font-weight: bold;
    font-size: 30px;
    text-transform: lowercase;
    background-color: #1e1e1e;
    border: 1px solid rgb(241, 177, 14);
    color: rgb(241, 177, 14);
    margin-top: 20px;
  }

  .home-button:hover {
    background-color: rgb(241, 177, 14);
    color: #1e1e1e;
  }
}

.breakpoint__small-down {
  .home-intro {
    font-size: 50px;
    font-weight: bold;
    padding: 5px 0;
  }

  .home-button {
    font-family: 'Raleway';
    font-weight: bold;
    font-size: 25px;
    text-transform: lowercase;
    background-color: #1e1e1e;
    border: 1px solid rgb(241, 177, 14);
    color: rgb(241, 177, 14);
    margin-top: 20px;
  }

  .home-button:hover {
    background-color: rgb(241, 177, 14);
    color: #1e1e1e;
  }
}

.home {
  display: flex;
  height: 100vh;
  align-items: center;
  color: rgb(241, 177, 14);
  padding: 0 50px;
}


.home-left {
  display: flex;
  flex-direction: column;
}

