@import url('https://fonts.googleapis.com/css2?family=PT+Sans+Caption:wght@700&family=Poppins:wght@200;400&family=Raleway:wght@100;600&family=Source+Sans+Pro:wght@200&family=Work+Sans:wght@400;500&display=swap');

.header {
  display: flex;
  align-items: center;
  width: 100%;
  position: fixed;
  top: 0;
  background-color: black;
  transition: 0.5s;
  z-index: 1;
}


.header-options {
  padding: 0 20px;
  font-size: 40px;
  font-weight: bold;
  color: rgb(241, 177, 14);
}

.header-options:hover {
  transition: 0.5s;
  cursor: pointer;
  color: lightgray;
}


.header-left {
  flex: 1;
}

.header-right {
  display: flex;
  justify-content: space-evenly;
}
