@import url('https://fonts.googleapis.com/css2?family=PT+Sans+Caption:wght@700&family=Poppins:wght@200;400&family=Raleway:wght@100;600&family=Source+Sans+Pro:wght@200&family=Work+Sans:wght@400;500&display=swap');

.breakpoint__large-up {
  .about {
    display: flex;
    padding: 40px 50px;
  }
  
  .about-right {
    flex: 0.5;
  }
  
  .about-left {
    flex: 0.5;
  }

  .about-title {
    padding-bottom: 40px;
  }

  .about-details {
    padding-top: 85px;
  }
}

.breakpoint__medium-down {
  .about {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px 50px;
  }

  .about-title {
    padding: 40px 0;
  }

  .about-details {
    padding: 40px 0;
  }

  .about-image {
    padding: 20px 0;
  }
}

.about-image {
  width: 500px;
  height: 350px;
}

.about-title {
  color: rgb(241, 177, 14);
  font-size: 40px;
  font-weight: bold;
}

.about-details {
  font-size: 18px;
  color: white;
}